module.exports = function (props) {
    // private

    const that = this;

    var state = { afterRender: [], elements: [], timeDelay: null, deconstructor: async () => {} }

    function init() {
        // load static styles
        props.method.styles().loadStyles();
        // set this to be this to the ui property so it can be accessed via the dom elements
        window.ui = that;
    }

    init();

    function display(data) {
        try {
            if(!data) {
                // full fanout rendering of all elements
                document.getElementById(props.target).innerHTML = props.method.layouts.default().compose({ elements: state.elements });
                // call each constructor from the afterRender array
                state.afterRender.forEach((d) => {
                    d();
                });
                // remove all constructors from the afterRender array
                state.afterRender = [];
            }
            else {
                // individual rendering of single element
                document.getElementById(`element-${data.element_id}`).innerHTML = data.element.display();
            }
            // apply styles of indidividual elements
            state.elements.forEach((d) => {
                // apply styles for each element if exists
                d.applyStyle();
            });
        }
        catch(e) {
            console.log(e);
        }
    }

    function remove(data) {
        state.elements.map((d, i) => {
            if(data.element_id === d.props._id) {
                state.elements.splice(i, 1);
            }
        });
        display();
    }

    function generate(data) {
        data.display = display;
        data.remove = remove;
        const element = data.element(data);
        state.elements.push(element);
        if(state.timeDelay) {
            clearTimeout(state.timeDelay);
        }
        state.timeDelay = setTimeout(() => { display() }, 1);
        return element;
    }

    function findElementById(data) {
        for(var i = 0; i < state.elements.length; ++i) {
            if(data.element_id === state.elements[i].props._id) {
                return state.elements[i];
            }
        }
    }

    // privileged

    this.displayElement = function(data) {
        // data = { element_id: "" }
        findElementById(data).displayElement();
    }

    this.displayOptions = function(data) {
        // data = { element_id: "" }
        findElementById(data).displayOptions();
    }

    this.selectOption = function(data) {
        // data = { element_id: "", option_id: "" }
        findElementById(data).selectOption(data);
    }

    this.updateElement = function(data) {
        // data = { element_id: "" }
        findElementById(data).updateData(data);
    }

    this.image = function(data) {
        data.element = props.method.elements.image;
        return generate(data);
    }

    this.video = function(data) {
        data.element = props.method.elements.video;
        return generate(data);
    }

    this.text = function(data) {
        data.element = props.method.elements.text;
        return generate(data);
    }

    this.form = function() {
        var data = {};
        data.element = props.method.elements.form;
        return generate(data);
    }

    this.camera = function(data) {
        data.element = props.method.elements.camera;
        return generate(data);
    }

    this.selection = function() {
        var data = {};
        data.element = props.method.elements.selection;
        return generate(data);
    }

    this.notify = function(data) {
        //state.error.notify(data);
        alert(data.body);
    }

    this.silence = function(data) {
        //state.error.silence();
    }

    this.clear = async function(data) {
        // call deconstructor before removing elements
        await state.deconstructor();
        // reset the deconstructor function to the default neutral
        state.deconstructor = async () => {};
        state.elements = [];
        display();
    }

    this.afterRender = function(data) {
        // { constructor: () => {} }
        // creates a method to call after elements are set
        state.afterRender.push(data.constructor);
    }

    this.deconstructor = function(data) {
        // { deconstructor: () => {} }
        // called just before clearing all elements
        state.deconstructor = data.deconstructor;
    }

  };
