module.exports = function(props) {
    // private

    const patterns = {
        tripleImage: ["image", "image", "image"],
        textImage: ["text", "image"],
        imageText: ["image", "text"],
        singleImage: ["image"],
        doubleImage: ["image", "image"]
    }

    function collect(data) {
        // { elements: [], index: i, pattern: [] }
        // collect elements associated with the pattern
        var collection = [];
        for(var i = 0; i < data.pattern.length; ++i) {
           collection.push(data.elements[i + data.index]);
        }
        return collection;
    }

    function mask(data) {
        // { elements: [], index: i, pattern: [] }
        // check for matching patterns associated with the elements
        if(data.pattern.length  > data.elements.length - data.index) {
            return false;
        }
        
        for(var i = 0; i < data.pattern.length; ++i) {
            // console.log(data.pattern[i]);
            if(data.pattern[i] !== data.elements[i + data.index].element_type) {
                return false;
            }
        }
        return true;
    }

    function tripleImage(data) {
        // { elements: [] }
        return `
<div class = "row">
  <div class="card sym-third" id=element-${data.elements[0].props._id}>
    ${data.elements[0].display()}
  </div>
  <div class="card sym-third" id=element-${data.elements[1].props._id}>
    ${data.elements[1].display()}
  </div>
  <div class="card sym-third" id=element-${data.elements[2].props._id}>
    ${data.elements[2].display()}
  </div>
</div>
`
    }

    function singleImage(data) {
      // { elements: [] }
      return `
<div class = "row">
<div class="card sym-third" id=element-${data.elements[0].props._id}>
  ${data.elements[0].display()}
</div>
</div>
`
  }

  function doubleImage(data) {
    // { elements: [] }
    return `
<div class = "row">
<div class="card sym-third" id=element-${data.elements[0].props._id}>
${data.elements[0].display()}
</div>
<div class="card sym-third" id=element-${data.elements[1].props._id}>
${data.elements[1].display()}
</div>
</div>
`
}

    function textImage(data) {
        // { elements: [] }
        return `
<div class = "row">
  <div class="card two-third" id=element-${data.elements[0].props._id}>
    ${data.elements[0].display()}
  </div>
  <div class="card asym-third" id=element-${data.elements[1].props._id}>
    ${data.elements[1].display()}
  </div>
<div>
`
    }

    function imageText(data) {
        // { elements: [] }
        return `
<div class = "row">
  <div class="card asym-third" id=element-${data.elements[0].props._id}>
    ${data.elements[0].display()}
  </div>
  <div class="card two-third" id=element-${data.elements[1].props._id}>
    ${data.elements[1].display()}
  </div>
<div>
`
    }

    function fullWidth(data) {
        // { elements: [] }
        return `
<div class = "row">
  <div class="card" id=element-${data.elements[0].props._id}>
    ${data.elements[0].display()}
  </div>
<div>
`
    } 

    // public

    // privileged

    this.compose = function(data) {
        // { elements: [] }
        // here we'll sort through the elements and assign rows and classes
        var composed = [];
        var eliminations = data.elements;
    
        for(var i = 0; i < eliminations.length; ++i) {
            // if width prop exists and is set to full go full width
            if(eliminations[i].props.hasOwnProperty("width") && eliminations[i].props.width === "full") {
              composed.push(fullWidth({ elements: [eliminations[i]] }));
              continue;
            }

            // triple image pattern
            if(mask({ elements: eliminations, index: i, pattern: patterns.tripleImage })) {
                // collect elements associated with the pattern
                const elements = collect({ elements: eliminations, index: i, pattern: patterns.tripleImage });
                // compose html elements
                composed.push(tripleImage({ elements: elements }));
                // increment past the adopted elements
                i += 2;
                continue;
            }
            // text image pattern
            if(mask({ elements: eliminations, index: i, pattern: patterns.textImage })) {
                // collect elements associated with the pattern
                const elements = collect({ elements: eliminations, index: i, pattern: patterns.textImage });
                // compose html elements
                composed.push(textImage({ elements: elements }));
                // increment past the adopted elements
                i += 1;
                continue;
            }
            // image text pattern
            if(mask({ elements: eliminations, index: i, pattern: patterns.imageText })) {
                // collect elements associated with the pattern
                const elements = collect({ elements: eliminations, index: i, pattern: patterns.imageText });
                // compose html elements
                composed.push(imageText({ elements: elements }));
                // increment past the adopted elements
                i += 1;
                continue;
            }
            // double image pattern
            if(mask({ elements: eliminations, index: i, pattern: patterns.doubleImage })) {
              // collect elements associated with the pattern
              const elements = collect({ elements: eliminations, index: i, pattern: patterns.doubleImage });
              // compose html elements
              composed.push(doubleImage({ elements: elements }));
              // increment past the adopted elements
              i += 1;
              continue;
          }
            // single image pattern
            if(mask({ elements: eliminations, index: i, pattern: patterns.singleImage })) {
              // collect elements associated with the pattern
              const elements = collect({ elements: eliminations, index: i, pattern: patterns.singleImage });
              // compose html elements
              composed.push(singleImage({ elements: elements }));
              // no increment being single element
              continue;
            }
            // all else will be full width
            composed.push(fullWidth({ elements: [eliminations[i]] }));
        }
        return composed.join(" ");
    }

  };
